import { reactive } from "vue";
import { THEME_STYLE_E } from "../../common/config";
import duiService from "../../services/duiService";

export let textareaState: Function = (props: any): object => {
    let state = reactive({
        focused: false, // 记录输入框是否已经聚焦过。
        onFocus: false, // 记录聚焦状态
        onOver: false, // 记录光标over状态
        disabled: false,
        error: false,
        icon: "unviewable",
        historyPosition: "bottom",
        cLevel: THEME_STYLE_E.PRIMARY,
    });

    return {
        state
    };
};

export default {
    textareaState
}